import React from "react";
import { Snackbar, IconButton } from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";
import MuiAlert from "@material-ui/lab/Alert";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export const MessageCard = ({ message, closeMessage }) => {
  return (
    <span>
      <Snackbar
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "right",
        }}
        open={Boolean(message)}
        autoHideDuration={6000}
      >
        <Alert
          style={{ background: message === "error" ? "#da1e28" : "#198038" }}
          severity="error"
          action={
            <React.Fragment>
              <IconButton
                onClick={() => closeMessage()}
                size="small"
                color="inherit"
              >
                <CloseIcon fontSize="small" />
              </IconButton>
            </React.Fragment>
          }
        >
          {message === "error"
            ? "ÐÑÐ¸Ð±ÐºÐ°!  ÐÑÐ¾Ð²ÐµÑÑÑÐµ Ð²Ð¿ÑÐ°Ð²Ð¸Ð»ÑÐ½Ð¾ÑÑÑ Ð²Ð²Ð¾Ð´Ð° Ð¸Ð»Ð¸ Ð¿Ð¾Ð´ÐºÐ»ÑÑÐµÐ½Ð¸Ðµ Ðº ÑÐµÑÐ¸"
            : "ÐÐ°Ð¿Ð¸ÑÑ ÑÑÐ¿ÐµÑÐ½Ð¾ ÑÐ¾Ð·Ð´Ð°Ð½Ð° "}
        </Alert>
      </Snackbar>
    </span>
  );
};
